.cases {
  background-repeat: no-repeat;
  background-position: top 1100px center;
  padding-bottom: 160px;
}

.cases-content {
}
.title-cust-h2 h2 {
  font-family: "Arial";
  font-style: normal;
  font-weight: 700;
  font-size: clamp(2.063rem, 0.403rem + 8.3vw, 6.625rem);
  line-height: clamp(1.563rem, -0.642rem + 11.02vw, 7.625rem);
  text-transform: uppercase;
  max-width: 1532px;
  white-space: nowrap;
  line-height: 130%;
  text-align: start;
}
.end {
  text-align: end;
  padding: 0 clamp(0rem, -9.091rem + 45.45vw, 25rem);
}
.start {
  text-align: start;
}

.title-cust-h2 {
  margin-bottom: 10px;
}

.cases-tabs {
  margin-bottom: 150px;
}

.tab-nav {
  position: relative;
  display: flex;
  justify-content: end;
}

.tab-nav li {
  margin-right: 140px;
}

.tab-nav li {
  a {
    font-family: "Arial";
    font-style: normal;
    font-weight: 400;
    font-size: 21px;
    text-transform: uppercase;
    color: #a1a1a1;
    display: inline-block;
  }
}

.tab-nav li {
}

.tab-nav li:last-child {
  margin-right: 0;
}
.tab-nav {
  margin-bottom: clamp(1.25rem, -1.705rem + 14.77vw, 9.375rem);
  white-space: nowrap;
}

.tab-nav a.active,
.tab-nav a:focus {
}

.tab-content .tab-pane {
  display: none;
}

.tab-content .tab-pane.active {
  display: block;
}

.tab-nav li a {
  position: relative;
  display: inline-block;
  padding-right: 20px; /* увеличиваем отступ справа, чтобы было место для неровного круга */
}
.tab-nav li a:last-child {
  padding-right: 0;
}

.tab-nav li a::before {
  content: "";
  position: absolute;
  top: 50%;
  right: 50%;
  transform: rotate(-14deg) translate(53%, -17%);
  width: clamp(16rem, 13.636rem + 11.82vw, 22.5rem);
  height: clamp(5rem, 4rem + 5vw, 7.75rem);
  border-radius: 50%;
  border: 3px solid #02ff4e;
  opacity: 0;
  transition: all 0.3s ease-in-out;
}

.tab-nav li a.active:before {
  opacity: 1; /* при наведении на ссылку или при ее активации круг становится видимым */
}

.circle2 {
  position: absolute;
  width: 360px;
  height: 140px;
  border-radius: 50%;
  background-color: rgb(255, 0, 0);
  z-index: -1;
  transition: all 0.3s ease; /* Это свойство задает плавную анимацию при перемещении круга */
}

////////////////////////////// cases

.cases-all {
}
.cases-card {
}
.cases-card-1 {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: relative;
  margin-bottom: clamp(0.625rem, -1.42rem + 10.23vw, 6.25rem);
}

.cases-img {
  margin-bottom: 12px;
}

.cases-title {
  font-family: "Arial";
  font-style: normal;
  font-weight: 700;
  font-size: clamp(2.188rem, 0.801rem + 6.93vw, 6rem);
  line-height: clamp(4.063rem, 3.04rem + 5.11vw, 6.875rem);
  text-transform: uppercase;
  text-align: end;
  margin-right: 20px;
  z-index: 5;
}

.cases-sub {
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 29px;
  max-width: 600px;
  text-align: right;

  color: #a1a1a1;
}
.cases-tag {
  padding: 5px 19px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 30px;
  margin-bottom: 30px;
  font-size: clamp(0.75rem, 0.659rem + 0.45vw, 1rem);
}

.cases-card-2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  margin-bottom: clamp(0.625rem, -1.42rem + 10.23vw, 6.25rem);
}

.cases-card-lin {
  display: block;
}

.cases-t {
  display: flex;
  align-items: center;
}
.cases-t-2 {
  display: flex;
  flex-direction: row-reverse;
}

.cases-sub-2 {
  text-align: start;
}

.cases-title-2 {
  margin-left: 20px;
  text-align: start;
}

.case-btn {
  margin-top: -350px;
}

.cases-img img {
  width: 100%;
  height: 527px;
  object-fit: cover;
  object-position: bottom;
}

.cases-title {
  max-width: 700px;
}

@media (max-width: 1300px) {
  .cases-t-2,
  .cases-t {
    flex-direction: column;
  }
  .cases-title {
    max-width: 100%;
    margin-bottom: 20px;
  }

  .cases-img img {
    height: clamp(10.313rem, 2.925rem + 36.94vw, 32.938rem);
  }

  .cases-sub {
    font-size: clamp(1.125rem, 0.989rem + 0.68vw, 1.5rem);
  }
  .title-cust-h2 {
    margin-bottom: 90px;
  }
  .tab-nav {
    display: flex;
    justify-content: center;
    flex-direction: column;
  }
  .tab-nav li {
    margin-right: 100px;
    margin-bottom: 50px;
    text-align: start;
  }
  .cases-t {
    justify-content: flex-end;
    align-items: flex-end;
  }
  .cases-t-2 {
    justify-content: flex-start;
    align-items: flex-start;
  }

  .case-btn {
    margin-top: -217px;
  }
}

@media (max-width: 1150px) {
  .case-btn {
    margin-top: -104px;
  }
}

@media (max-width: 780px) {
  .case-btn {
    margin-top: 20px;
  }
  .cases {
    background-repeat: no-repeat;
    background-position: top 900px center;
    padding-bottom: 160px;
    background-size: 300%;
  }
}
