.footer-content{
  display: flex;
  align-items: start;

}
.footer__title-text h2{
  font-style: normal;
  font-weight: 400;
  font-size: clamp(1.813rem, 0.29rem + 7.61vw, 6rem);
  line-height: 100%;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  margin-bottom: 57px;
}

.footer__title-text p{
  font-style: normal;
  font-weight: 400;
  font-size: clamp(1.125rem, 0.943rem + 0.91vw, 1.625rem);
  line-height: 139.5%;
  /* or 36px */
  max-width: 820px;
  margin-bottom: 160px;
}

.flex{
  display: flex;
  justify-content: space-between;
  margin-bottom: 70px;
  align-items: start;
}

.footer-link1{
  margin-right: 200px;

}

.footer-footer{
  display: flex;
  justify-content: space-between;
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  line-height: 100%;
  letter-spacing: 2px;
  text-transform: uppercase;
  margin-bottom: 60px;
}

.right{
  display: flex;
}

.privacy{
  margin-right: 30px;
}

.footer__title-link{
  font-weight: 400;
  font-size: 20px;
  letter-spacing: 1px;
  text-transform: uppercase;

  margin-bottom: 30px;

}

.linlk-footer__item{
  margin-bottom: 30px;
  font-style: normal;
  font-weight: 400;
  font-size: 13px;
  line-height: 100%;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.link-footer__list{
  max-width: 200px;
}

.footer-container{
  display: flex;
}

html,body{
  height: 100%;
}


.main{
  flex-grow: 1;
}



@media (max-width:768px) {
  .nav-menu {
    margin-top: 25px;
}
  .flex {
    flex-direction: column;
}

.logo-contianer-footer {
  margin-bottom: 30px;
}

.copyright{
  line-height: 130%;
  margin-bottom: 20px;
}
.footer-footer{
  flex-direction: column;
}
.footer-container{
  width: 100%;
  justify-content: space-between;
}
.footer-link1 {
  margin-right: 0;
}

}

@media (max-width:375px) {


.footer-container{
  flex-direction: column;
}
.footer-link1{
  margin-bottom: 20px;
}

}

@media (max-width:1300px) {

  .footer-content {
    flex-direction: column;
  }
  .footer__title-text p {
    font-style: normal;
    font-weight: 400;
    font-size: clamp(1.125rem, 0.943rem + 0.91vw, 1.625rem);
    max-width: 820px;
}
.footer__title-text p {
  font-style: normal;
  font-weight: 400;
  font-size: clamp(1.125rem, 0.943rem + 0.91vw, 1.625rem);
  line-height: 139.5%;
  max-width: 820px;
  margin-bottom: 40px;
}
.footer__btn{
  margin-bottom: 50px;
}
  }










