.header {
  z-index: 111;
  width: 100%;
  margin-bottom: clamp(1.938rem, -0.767rem + 13.52vw, 9.375rem);
}
.nav-menu {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;



}

.logo-contianer{
 display: block;
 z-index: 10;
}
.logo img {
  display: block;
  z-index:10;
}

// .header__logo

// .header__btn

.menu {
  position: absolute;
  z-index: 20;
  top: 0%;
  left: -100%;
  height: 100vh;
  width:30%;
  display: flex;
  overflow: auto;
  padding: 20px 0;
  transition: left 0.4s;
  background-color: #21196d;

  &._active {
    left: 0;
  }
  &-icon {

    top: 6px;
    display: block;
    height: 18px;
    width: clamp(2.563rem, 1.631rem + 4.66vw, 5.125rem);
    position: relative;
    cursor: pointer;
    z-index: 50;
    span {
      transition: all 0.3s ease 0s;
      top: calc(50% - 1px);
      right: 0;
      position: absolute;
      width: 100%;
      height: 3px;
      background-color: #fff;
      &:first-child {
        top: 0;
      }
      &:last-child {
        top: auto;
        bottom: 0;
        width: clamp(1.938rem, 1.21rem + 3.64vw, 3.938rem);
      }
    }
    &._active {
      span {
        transform: scale(0);
        &:first-child {
          transform: rotate(-45deg);
          top: calc(50% - 1px);
          width: 50px;
        }
        &:last-child {
          transform: rotate(45deg);
          bottom: calc(50% - 1px);
          width: 50px;
        }
      }
    }
  }
  // .menu__body
  &__body {
    display: flex;
    align-items: start;

    flex-direction: column;
    margin: auto;
  }
  // .menu__item
  &__item{
    margin-bottom: 20px;
    &:last-child{
      margin-bottom: 0;
    }
  a{
    color: #fff;

  }
  }
  // .menu__link
  &__link {
    font-weight: 500;
    font-size: 16px;
    text-transform: capitalize;
    text-align: start;
    text-decoration: none;
    transition: color 0.2s;
    position: relative;
    &:before {
      content: "";
      position: absolute;
      bottom: -5px;
      left: 0;
      right: 0;
      height: 3px;
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.2s;
    }
    &:hover {
      color: #ff78bc;
      &:before {
        opacity: 1;
        visibility: visible;
      }
    }
    line-height: 40px;
    font-size: 20px;
  }
}

.header{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 0 0 15px 0;
  z-index: 111;

  transition: background-color 0.5s ease-in-out;
}

header.scrolled {
  background-color: rgba(57, 42, 81, 0.5);
}



@media (max-width: 1024px) {
  .menu{
    width:60%;
  }
}

@media (max-width: 400px) {
  .menu{
    width:60%;
  }
}
