.advantages {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;

  border-radius: 90px;
  position: relative;
  height: 100%;
  margin-bottom: 30px;
  z-index: 1;



}

.advantages-content {
  padding: 0 50px;

}
.advantages-boxs {
  display: flex;
  padding-top: clamp(6.25rem, 2.841rem + 17.05vw, 15.625rem);
  justify-content: center;
  margin-bottom: clamp(1.25rem, -5.114rem + 31.82vw, 18.75rem);
  z-index: 5;
}

.advantages-box-1 {
}
.advantages-box {
  text-align: center;
  position: relative;
  border-radius: 50%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  padding: 5px;
}
.ad-title {
  font-size: clamp(1.563rem, 1.222rem + 1.7vw, 2.5rem);
  margin-bottom: 16px;
  max-width: 320px;
}
.ad-sub {
  font-weight: 500;
  font-size: clamp(1rem, 0.818rem + 0.91vw, 1.5rem);
  max-width: 320px;
  margin: 0 auto;
  text-align: center;
  color: #ffffff;
  line-height: 140%;
}
.advantages__button-el {
  padding-bottom: 83px;
}
.qwe {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: clamp(15rem, 12.5rem + 12.5vw, 21.875rem);
  height: clamp(15rem, 12.5rem + 12.5vw, 21.875rem);
  display: block;
  border-radius: 50%;
  border: 2px solid #ffffff;
}
