@media (max-width: 1540px) {
  .advantages-boxs {
    overflow-x:auto;
  }

  .advantages-boxs::-webkit-scrollbar {
    height: 0;  /* Скрыть полосу скролла для Chrome и Safari */
  }

  .advantages-boxs::-moz-scrollbar {
    height: 0;  /* Скрыть полосу скролла для Firefox */
  }

  .advantages-boxs {
    justify-content: flex-start;
  }

  .advantages-box {
    margin-bottom: 90px;
  }

  .advantages-boxs {
    padding-left: 15px;
  }
}

@media (max-width: 1730px) {
  .ad-sub {
    width: clamp(13.125rem, 9.943rem + 15.91vw, 21.875rem);
  }
}

@media (max-width: 1150px) {
  .advantages-content {
    padding: 0;
  }
}

@media (max-width: 1175px) {
  .logo-ad {
    margin-bottom: 20px;
  }
  .advantages__button-el {
    display: flex;
    flex-direction: column;
  }
}

.counter {

  /* Анимация */
  animation-name: count-up;
  animation-duration: 5s;
  animation-timing-function: ease-out;
}

/* Ключевая кадры для анимации */
@keyframes count-up {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}
