// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Montserrat", sans-serif;
  --content-width: 1760px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
}

// Нстройка шрифта
$font-size-h2:clamp(1.25rem, 0.25rem + 5vw, 4rem);
$font-size-p:clamp(1.125rem, 0.989rem + 0.68vw, 1.5rem);

// Настройка цвета
$main-bg-color:#0B0B0B;

