.partners {
  position: relative;

  background-repeat: no-repeat;
  background-position: top 100px;
  margin-bottom: 166px;
}

.partners-img {
  position: absolute;
  z-index: -5;
}

.partners-h2 h2 {
  font-family: "Arial";
  font-style: normal;
  font-weight: 700;
  font-size: clamp(2.063rem, 0.403rem + 8.3vw, 6.625rem);
  line-height: 130%;
  text-transform: uppercase;
  margin-bottom: 38px;
}
.indent {
  margin-left: 50px;
}

.companies-card {
  display: flex;
  justify-content: space-between;
  width: 1070px;
  align-items: center;
  align-self: flex-start;
  padding: 21px 0;
  border-top: 1px solid rgb(255, 255, 255);
  border-bottom: 1px solid rgb(255, 255, 255);
}
.companies-text {
  max-width: 544px;
}

.companies-card {
  display: flex;
  justify-content: space-between;
}

.logo-companies img {
  display: block;

  object-fit: unset;
}

.logo-companies {
  margin-right: 40px;
  width: 260px;
}

.companies-icon {
  margin-top: 5px;
  align-self: flex-start;
}

.companies-icon img {
  width: 24px;
  height: 24px;
  display: block;
  margin-right: 10px;
  object-fit: unset;
}

.content-companies {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  justify-content: space-between;
}

.df {
  display: flex;
  align-items: center;
}

@media (max-width: 1100px) {
  .companies-card {
    width: 100%;
  }
}
@media (max-width: 870px) {
  .df {
    flex-direction: column;
    align-items: flex-start;
  }

  .content-companies {
    justify-content: center;
  }

  .logo-companies img {
    margin-bottom: 20px;
  }
  .companies-text {
  }
}

@media (max-width: 360px) {
  .companies-card {
    position: relative;
  }

  .companies-icon {
    position: absolute;
    top: 15px;
    right: 0;
  }

  .companies-icon img {
    width: 15px;
    height: 15px;
  }
}

@media (max-width: 1500px) {
  .content-companies {
    flex-direction: column-reverse;
    align-content: center;
    align-items: flex-start;
  }

  .companies-cards {
    margin-bottom: 30px;
  }
}

.content-partners {
  position: relative;
  margin-bottom: 200px;
}
.partner__btn {
  display: block;

}

.partner__btn img {
  display: block;

}

.partners {

}



.text-text{
  padding: 16px 0;
  background-color: green;


}
.text-text p{
  font-weight: 400;
  font-size: 14px;

  color: #0B0B0B;
  text-transform: uppercase;

}
