.about{
  width: 100%;
  background-color: $main-bg-color;
  z-index: 1;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;

  background-position: center center;
  min-height: 737px;
}

@media (max-width:768px) {
  .about{
  background-size:180% 180%;
  }
}

.about-content {
  padding-top: 70px;
  display: flex;
  justify-content: space-between;
  align-items: end;
  margin-bottom: 70px;
}
.title-h2 h2{
  font-family: 'Arial';
  font-style: normal;
  font-weight: 700;
  font-size: clamp(1.5rem, -0.136rem + 8.18vw, 6rem);
  line-height: 139.5%;
  text-transform: uppercase;
  color: #FFFFFF;
  max-width: 700px;
  margin-bottom: 48px;

}
.about-sub{
  font-style: normal;
  font-weight: 300;
  font-size: clamp(1rem, 0.818rem + 0.91vw, 1.5rem);
  line-height: 140%;
  max-width: 460px;
  /* or 34px */


  color: #FFFFFF;
}
.about__text-content {

}
.about-p-1{
  font-size: clamp(1rem, 0.818rem + 0.91vw, 1.5rem);
  line-height: 140%;
  color: #FFFFFF;
  max-width: 873px;
  margin-bottom: 74px;
}
.about-p-2{
  font-size: clamp(1rem, 0.818rem + 0.91vw, 1.5rem);
  line-height: 140%;
  color: #FFFFFF;
  max-width: 873px;
}


.about-img {
}
.about-img1 {

}
.about-img2 {
}


@media (max-width: 1480px) {
  .about-content{
    flex-direction: column;
    align-items: start;
  }
.about-sub{
  margin-bottom: 30px;
}
  .about-p-1{
    margin-bottom: 30px;
  }

}
