.color-a {
  top: -650px;
  position: absolute;
  animation-name: move;
  animation-duration: 16s;
  animation-iteration-count: infinite;
  z-index: -1;
}

.main-screen__image{
  margin-bottom: 25px;
}

@keyframes move {
  0% {
    transform: translate(0, 0);
  }
  25% {
    transform: translate(80%, -0%);
  }
  50% {
    transform: translate(-20%, 80%);
  }
  75% {
    transform: translate(5%, 0);
  }
  100% {
    transform: translate(0, 0);
  }
}

.main-screen__container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  position: relative;
}

.main-screen__image {
  position: absolute;
  right: 0;
  bottom: 11%;
}
.main-screen__btn {
  display: block;
  border-radius: 50%;
  animation-name: spin;
  animation-duration: 20s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  margin-bottom: -50px;
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.title {
  margin-bottom: clamp(1.25rem, 0.705rem + 2.73vw, 2.75rem);
}

.title {
  max-width: 1200px;
  h1 {
    font-family: "Arial";
    font-style: normal;
    font-weight: 700;
    font-size: clamp(2rem, -0.182rem + 10.91vw, 8rem);
    text-transform: uppercase;
    color: #ffffff;
    line-height: clamp(2.813rem, 0.767rem + 10.23vw, 8.438rem);
  }
}

.sub-content {
  display: block;
  max-width: 766px;
  font-style: normal;
  font-weight: 500;
  font-size: clamp(1.25rem, 0.977rem + 1.36vw, 2rem);
  line-height: clamp(2.5rem, 2.091rem + 2.05vw, 3.625rem);
  /* or 182% */
  align-items: center;
  color: #ffffff;
  margin-bottom: 120px;
}

.main-screen__btn {
  display: block;
  width: clamp(12.5rem, 9.091rem + 17.05vw, 21.875rem);
}

@media (max-width: 1042px) {
  .img-bc-2 {
    display: none;
  }

  .main-screen__btn {
    margin-bottom: 20px;
  }
  .main-screen__image {
    position: static;
  }

  .main-screen__container {
    display: flex;
    flex-direction: column;
    align-items: start;
  }
  .sub-content {
    margin-bottom: 25px;
  }
  .main-screen__image {
  }
}

