  padding-left: 0; /* Отступ слева в браузере Firefox, Safari, Chrome */
}

h1, h2, h3, h4, h5, p {
  margin: 0;
  padding: 0;
}

.site-container {
  overflow: hidden;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
