.menu {
  position: absolute;
  z-index: 20;
  top: 0%;
  left: -100%;
  height: 100vh;
  width: 30%;
  display: flex;
  overflow: auto;
  padding: 20px 0;
  transition: left 0.4s;
  background-color: #21196d;
}
.menu._active {
  left: 0;
}
.menu-icon {
  top: 6px;
  display: block;
  height: 18px;
  width: clamp(2.563rem, 1.631rem + 4.66vw, 5.125rem);
  position: relative;
  cursor: pointer;
  z-index: 50;
}
.menu-icon span {
  transition: all 0.3s ease 0s;
  top: calc(50% - 1px);
  right: 0;
  position: absolute;
  width: 100%;
  height: 3px;
  background-color: #fff;
}
.menu-icon span:first-child {
  top: 0;
}
.menu-icon span:last-child {
  top: auto;
  bottom: 0;
  width: clamp(1.938rem, 1.21rem + 3.64vw, 3.938rem);
}
.menu-icon._active span {
  transform: scale(0);
}
.menu-icon._active span:first-child {
  transform: rotate(-45deg);
  top: calc(50% - 1px);
  width: 50px;
}
.menu-icon._active span:last-child {
  transform: rotate(45deg);
  bottom: calc(50% - 1px);
  width: 50px;
}
.menu__body {
  display: flex;
  align-items: start;
  flex-direction: column;
  margin: auto;
}
.menu__item {
  margin-bottom: 20px;
}
.menu__item:last-child {
  margin-bottom: 0;
}
.menu__item a {
  color: #fff;
}
.menu__link {
  font-weight: 500;
  font-size: 16px;
  text-transform: capitalize;
  text-align: start;
  text-decoration: none;
  transition: color 0.2s;
  position: relative;
  line-height: 40px;
  font-size: 20px;
}
.menu__link:before {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  right: 0;
  height: 3px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.2s;
}
.menu__link:hover {
  color: #ff78bc;
}
.menu__link:hover:before {
  opacity: 1;
  visibility: visible;
}

