.info-box__content {
  display: flex;
  justify-content: end;
}
.card-title-1 h2 {
  font-family: "Arial";
  font-style: normal;
  font-weight: 700;
  font-size: clamp(1.25rem, 0.25rem + 5vw, 4rem);
  line-height: 139.5%;
  max-width: 1020px;
  text-transform: uppercase;
  text-align: right;

  margin-top: 80px;
  margin-bottom: 150px;
}

.info-box__jmg img {
  position: absolute;
  width: 600px;
