  margin-bottom: 30px;
}
.card-info-box__p p {
  font-style: normal;
  font-weight: 500;
  font-size: clamp(1rem, 0.818rem + 0.91vw, 1.5rem);
  line-height: 140%;
  max-width: 1070px;
  margin-bottom: 70px;
}

[data-aos="new-a"] {
  transform: translateY(200px);

  @media screen and (min-width: 768px) {
    transform: translateY(200px);

    &.aos-animate {
      transform: translateY(500px);
    }
  }
}
[data-aos="new-a"].aos-animate {
  transform: translateY(0);
}

